import { createRouter, createWebHashHistory } from "vue-router";
import ZHome from "../views/Home.vue";

const routes = [
  {
    path: "/",
    name: "ZHome",
    component: ZHome,
  },
  {
    path: "/Js",
    name: "Js",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ "../views/Js.vue"),
  },
  {
    path: "/Html",
    name: "Html",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Html.vue"),
  },
  {
    path: "/Css",
    name: "Css",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ "../views/Css.vue"),
  },
  {
    path: "/Sass",
    name: "Sass",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () =>
      import(/* webpackChunkName: "about" */ "../views/Sass.vue"),
  },
  {
    path: "/Vue",
    name: "Vue",
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ "../views/Vue.vue"),
  },
];

const router = createRouter({
  history: createWebHashHistory(),
  routes,
});

export default router;
